/**
 * Dialog
 * <uo-dialog>
 *
 * Attributes:
 * heading
 * size
 * open
 */

// Import dependencies
import { unsafeCSS, css, html, LitElement } from 'lit';
import { unsafeHTML } from 'lit/directives/unsafe-html.js';

// Import animations
import { fadeOut } from '@automator/animations';

// Import style for web component
const styles = require( './web-component-style.scss' );

// eslint-disable-next-line require-jsdoc
export class AutomatorDialog extends LitElement {
	// Define style
	static styles = css`${ unsafeCSS( styles ) }`;

	// eslint-disable-next-line require-jsdoc
	static properties = {
		heading: { type: String, reflect: true },
		size: { type: String, reflect: true },
		open: { type: Boolean, reflect: true },
	};

	// eslint-disable-next-line require-jsdoc
	constructor() {
		super();

		// Define the action attribute
		this.actionAttribute = 'dialog-action';
	}

	// eslint-disable-next-line require-jsdoc
	render() {
		return html`
			<div 
				class="wrapper"
				?open=${ this.open }
			>
				<div 
					class="dialog"
					size="${ this.size }"
				>
					<div class="dialog-heading">${ unsafeHTML( this.heading ) }</div>

					<div class="dialog-content">
						<slot></slot>
					</div>

					<div class="dialog-footer" @click=${ this._handleClick }>
						<slot name="secondary-action"></slot>
						<slot name="primary-action"></slot>
					</div>
				</div>
			</div>
		`;
	}

	/**
	 * Disable scroll on body when the dialog is added
	 * 
	 * @return {undefined}
	 */
	connectedCallback() {
		super.connectedCallback();
		
		// Disable scroll on body element
		document.body.classList.add( 'uap-disable-scroll' );
	}

	/**
	 * Enable scroll on body when the dialog is added
	 * 
	 * @return {undefined}
	 */
	disconnectedCallback() {
		super.connectedCallback();
		
		// Disable scroll on body element
		document.body.classList.remove( 'uap-disable-scroll' );
	}

	/**
	 * Closes and destroys the modal
	 *
	 * @return {undefined}
	 */
	close() {
		// Fade out container
		fadeOut( this, () => {
			// Delete the node
			this.parentNode.removeChild( this );
		} );

		// Enable scroll on body
		document.body.classList.remove( 'uap-disable-scroll' );
	}

	/**
	 * Handles the action cancellation
	 *
	 * @return {undefined}
	 */
	cancel() {
		// Close the modal
		this.close();
	}

	/**
	 * Listens and handles clicks on the actions
	 *
	 * @param {Event} event The click event
	 * @return {undefined}
	 */
	_handleClick( event ) {
		// Check if the user is trying to cancel the action
		if ( event.target.closest( `[${ this.actionAttribute }="close"]` ) ) {
			// Cancel the action
			this.cancel();
		}
	}
}

customElements.define( 'uo-dialog', AutomatorDialog );
